.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.Feedback {
  width: 400px;
  padding: 10px;
  border: 1px solid black;
  text-align: center;
}

.Feedback_controls > button {
  border: none;
  padding: 10px 15px;
  background-color: teal;
  color: #fff;
  font-weight: 500;
  margin-left: 5px;
  margin-right: 5px;
  font-family: inherit;
  cursor: pointer;
  outline: none;
  box-shadow: 1px 2px 2px 0 rgb(0 0 0 / 0.5);
  transition: box-shadow 250ms linear;
}

.Feedback_controls > button:active {
  box-shadow: 2px 5px 2px 0 rgb(0 0 0 / 0.5);
}

.Feedback_text {
  margin-right: 15px;
  margin-left: 15px;
  font-size: 24px;
  font-family: monospace;
  text-align: center;
}

.Feedback_value {
  margin-right: 15px;
  margin-left: 15px;
  font-size: 14px;
  font-family: monospace;
  text-align: center;
}
